import { Dialog } from "@mui/material";
import React from "react";
import BasicTabs from "./BasicTabs";

const DialogFilter = ({ filter, onFilter, onClose, open }) => {
  return (
    <Dialog
      maxWidth="xl"
      onClose={onClose}
      open={open}
      sx={{
        ".MuiDialog-paper": {
          width: "32em",
          maxHeight: "100vh",
          minHeight: "70vh",
        },
      }}
    >
      <BasicTabs onClose={onClose} filter={filter} onFilter={onFilter}/>
    </Dialog>
  );
};

export default DialogFilter;