import { Box, Container, Button, Typography } from "@mui/material";
import { useState } from "react";
import Select from "react-select";
import data from "../../extra/data/data";

function labelize(nice) {
  if (nice !== undefined) 
  return nice.map((i) => ({ value: i, label: i }));
  else return [];
}

const BasicFilter = (props) => {
  let [filter, setFilter] = useState(props.filter);
  const depts = labelize(["CS", "SC", "IS", "CSys"]);
  let sections = labelize([0, 1, 2, 3, 4, 5, 6]);
  sections[0].label = "Lectures";
  let subs = data
    .map((i) => i.subject)
    .reduce((res, current) => {
      if (!res.includes(current)) {
        return [...res, current];
      } else return res;
    }, []);
  const subjects = labelize(subs);
  let defaultSections = labelize(filter.whom).map(
    i => {
      if (i.value === 0) {
        return {...i, label: "Lectures"}
      } else 
      return i
    }
  )
  return (
    <Box
      sx={{
        p: 2,
        ".select, .MuiTypography-root": {
          m: 2,
        },
      }}
    >
      <Typography>Department</Typography>
      <Select
        className="select"
        defaultValue={labelize(filter.dept)}
        onChange={(val) =>
          setFilter((filt) => ({ ...filt, dept: val.map((v) => v.value) }))
        }
        isMulti
        options={depts}
      />

      <Typography>Section</Typography>
      <Select
        className="select"
        defaultValue={defaultSections}
        onChange={(val) =>
          setFilter((filt) => ({ ...filt, whom: val.map((v) => v.value) }))
        }
        isMulti
        options={sections}
      />

      <Typography>Subject</Typography>
      <Select
        className="select"
        defaultValue={labelize(filter.subject)}
        onChange={(val) =>
          setFilter((filt) => ({ ...filt, subject: val.map((v) => v.value) }))
        }
        isMulti
        options={subjects}
      />

      <Container sx={{ margin: "20px 0", textAlign: "center" }} maxWidth="xl">
        <Button
          onClick={() => {
            props.onFilter(filter); props.onClose()
          }}
          variant="contained"
        >
          Apply
        </Button>
      </Container>
    </Box>
  );
};

export default BasicFilter;