import TableCell from '@mui/material/TableCell';

function Row(props){
  let data = props.data.sort(compareTimes);
  data = new Array(6).fill(0);
  for (let d of props.data){
    data[d.time - 1] = d;
  }
  let slots = data.map(
    (lect, index) => {
      if (lect == 0)
        return <TableCell key={index} className="empty"></TableCell>;
        
      let dept = lect.dept.join === undefined ? lect.dept : lect.dept.join('+')
      let content = [lect.subject]
      if (!props.filter.dept || props.filter.dept.length !== 1) content.push(dept)
      if (lect.whom != 0) content.push(lect.whom)
      if (lect.who) content.push(lect.who)
      content = content.map((el, i) => <span key={i}>{el}</span>)
      
      let classes = ["cell"]
      if (lect.whom == 0) {classes.push("lecture")}
      return (
      <TableCell key={index} className={(classes.join(" "))}>
        {content}
      </TableCell>)
  });
  return slots;
}


function compareTimes(a, b){
  return Number(a.time) - Number(b.time);
}

export default Row;
