import Table from "@mui/material/Table";
import TableHead from "@mui/material/TableHead";
import TableCell from "@mui/material/TableCell";
import TableRow from "@mui/material/TableRow";
import TableBody from "@mui/material/TableBody";
import Day from "./Day";
import data from "../extra/data/data";
import { tableslots as slots } from "../times.js";
import { getIndices, dayIndices } from "./helpers";

function TableView(props) {
  let indices = getIndices(data, props.filter)

  let days = {};
  for (let ind of indices) {
    if (!days[data[ind].day]) days[data[ind].day] = [];
    days[data[ind].day].push(data[ind]);
  }

  let dayNames = Object.keys(days);
  dayNames = dayNames.sort(compareDays);

  let heads = slots.map((slot, i) => (
    <TableCell
      key={i}
      sx={{
        textAlign: "center",
        border: 1,
      }}
    >
      {slot}
    </TableCell>
  ));
  return (
    <Table sx={{ textAlign: "center" }}>
      <TableHead>
        <TableRow>{heads}</TableRow>
      </TableHead>
      <TableBody>
        {dayNames.map((day, count) => (
          <Day
            name={day}
            key={count++}
            data={days[day]}
            filter={props.filter}
          />
        ))}
      </TableBody>
    </Table>
  );
}

function compareDays(a, b) {
  return dayIndices.indexOf(a) - dayIndices.indexOf(b);
}

export default TableView;
