import { Box, TextField, Button } from "@mui/material";
import { useState } from "react";

export const AdvancedFilter = (props) => {
  let [filter, setFilter] = useState(props.filter["_advanced"]);

  return (
    <Box
      sx={{
        padding: 5,
        width: "100%",
        overflow: "hidden",
        ".MuiTextField-root, .MuiButton-root": {
          margin: "15px 0"
        },
      }}
    >
      <TextField
        multiline
        sx={{
          width: "100%"
        }}
        onChange={(i) => setFilter(i.target.value)}
        value={filter}
      />
      <Button
        variant="contained"
        sx={{
          margin: "0 auto",
        }}
        onClick={() => {props.onFilter(filter); props.onClose()}}
      >
        Apply
      </Button>
    </Box>
  );
};
